import {
  Controller,
  Get,
  Post,
  Patch,
  Delete,
  Param,
  Body,
  Query,
  UseGuards,
} from '@nestjs/common';
import { RMQService } from 'nestjs-rmq';
import { JWTAuthGuard } from '../guards/jwt.guard';
import { UserId } from '../guards/user.decorator';

import {
  AccountCreate,
  AccountList,
  AccountGet,
  AccountUpdate,
  AccountDelete,
} from '@moneytracker/contracts';

import { CreateAccountDto } from '../dtos/create-account.dto';
import { UpdateAccountDto } from '../dtos/update-account.dto';
import { ListAccountsDto } from '../dtos/list-accounts.dto';
import { AccountIdDto } from '../dtos/account-id.dto';

@Controller('accounts')
export class WalletController {
  constructor(private readonly rmqService: RMQService) {}

  // 1) –ü–æ–ª—É—á–∏—Ç—å —Å–ø–∏—Å–æ–∫ —Å—á–µ—Ç–æ–≤ (—Å–≤–æ–∏—Ö –∏ peers), –≤–æ–∑–≤—Ä–∞—â–∞–µ–º —Ç–æ–ª—å–∫–æ –Ω—É–∂–Ω—ã–µ –ø–æ–ª—è
  @UseGuards(JWTAuthGuard)
  @Get()
  async list(
    @UserId() userId: string,
    @Query() dto: ListAccountsDto,
  ) {
    const response = await this.rmqService.send<
      AccountList.Request,
      AccountList.Response
    >(AccountList.topic, { userId, peers: dto.peers || [] });

    const sanitized = response.accounts.map(account => {
      const { _id, name, type, balance, currency } = account;
      return { _id, name, type, balance, currency };
    });

    return { accounts: sanitized };
  }

  // 2) –°–æ–∑–¥–∞—Ç—å –Ω–æ–≤—ã–π —Å—á–µ—Ç
  @UseGuards(JWTAuthGuard)
  @Post()
  async create(
    @UserId() userId: string,
    @Body() dto: CreateAccountDto,
  ) {
    return this.rmqService.send<AccountCreate.Request, AccountCreate.Response>(
      AccountCreate.topic,
      { userId, ...dto },
    );
  }

  // 3) –ü–æ–ª—É—á–∏—Ç—å –æ–¥–∏–Ω —Å—á–µ—Ç –ø–æ ID, –≤–æ–∑–≤—Ä–∞—â–∞—è —Ç–æ–ª—å–∫–æ –æ–ø—Ä–µ–¥–µ–ª—ë–Ω–Ω—ã–µ –ø–æ–ª—è
  @UseGuards(JWTAuthGuard)
  @Get(':id')
  async getById(
    @UserId() userId: string,
    @Param() params: AccountIdDto,
  ) {
    const response = await this.rmqService.send<
      AccountGet.Request,
      AccountGet.Response
    >(AccountGet.topic, { userId, id: params.id });
    const { account } = response;
    const { _id, name, type, balance, currency } = account;
    return { account: { _id, name, type, balance, currency } };
  }

  // 4) –û–±–Ω–æ–≤–∏—Ç—å —Å—á–µ—Ç –ø–æ ID
  @UseGuards(JWTAuthGuard)
  @Patch(':id')
  async update(
    @UserId() userId: string,
    @Param() params: AccountIdDto,
    @Body() dto: UpdateAccountDto,
  ) {
    return this.rmqService.send<
      AccountUpdate.Request,
      AccountUpdate.Response
    >(AccountUpdate.topic, {
      userId,
      id: params.id,
      ...dto,
    });
  }

  // 5) –£–¥–∞–ª–∏—Ç—å —Å—á–µ—Ç (soft-delete) –ø–æ ID
  @UseGuards(JWTAuthGuard)
  @Delete(':id')
  async delete(
    @UserId() userId: string,
    @Param() params: AccountIdDto,
  ) {
    return this.rmqService.send<
      AccountDelete.Request,
      AccountDelete.Response
    >(AccountDelete.topic, {
      userId,
      id: params.id,
    });
  }
}